html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
  vertical-align: baseline;
  font-family: 'Ubuntu', monospace;
  font-size: 1.5rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

#root {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
  margin: 0 auto;
  background: #9e9e9e;
  max-width: 80%;
  padding: 5px;
}

.display {
  background: #e6e6e6;
  padding: 0.5rem;
}

.result-display {
  margin-bottom: 1rem;
}

.display-item {
  text-align: right;
}

@media screen and (min-width: 768px) {
  body,
  html {
    font-size: 2rem;
  }
}

.buttons-container {
  display: flex;
  flex-wrap: wrap;
}

.single-button {
  flex-basis: 23%;
  background-color: #6c798b;
  margin: 1%;
  height: 60px;
  border-radius: 5px;
  border: none;
  outline: none;
  font-size: 1rem;
}

.single-button:hover {
  font-weight: 700;
}

.clear {
  flex-basis: 48%;
  background-color: #7A3A3A;
  color: #E6E1E1;
  font-weight: 700;
  text-transform: uppercase;
}

.clear:hover {
  text-shadow: 0 0 0.3rem #000, 0 0 0.3rem #000;
}

#equal {
  font-weight: 800;
  background-color: #5C5757;
  color: #E6E1E1;
}

#equal:hover {
  color: #aa9f9f;
}

.single-button:active {
  -webkit-transform: translateY(3px);
          transform: translateY(3px);
}

.footer {
  margin-top: 5%;
  text-align: center;
}

.footer-title p {
  font-size: 0.5rem;
}

.footer-icons {
  margin-top: 2%;
}

.link {
  padding: 0.25rem;
  margin: 0 3%;
  background: #6c798b;
  color: #5C5757;
  border-radius: 5px;
  border: none;

}

.link a {
  text-decoration: none;
  font-size: 0.6rem;
  color: #E6E1E1;
}

.link:hover a {
  font-weight: 700;
}
